import React, { Fragment } from 'react';
import Link from 'next/link';

const DoubleBlogPost = ({ even, post }) => {
  return (
    <Fragment>
      <Link prefetch href={`blog/${post.fields.slug}`}>
      <div className={`post ${even ? '--is-even' : ''}`}>
        <div className="post__description">
          {post.fields.description}
        </div>
        <div className="post__image"></div>
      </div>
      </Link>
      <style jsx>{`

        .post {
          background: red;
          display: flex;
        }

        .post.--is-even {
          background: green;
          align-self: flex-end;
        }

        .post > div {
          width: 300px;
          height: 300px;
          //box-shadow: 5px 5px 20px rgba(0,0,0,.2);
        }

        .post__description {
          background: white;
        }

        .post__image {
          background: url(${post.fields.heroImage.fields.file.url}?w=300&h=300&fit=thumb);
        }

        .post.--is-even .post__image {
          order: -1;
        }

      `}</style>
    </Fragment>
  );
};

export default DoubleBlogPost;
